import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NgxPaginationModule } from 'ngx-pagination';
import { SharedModule } from '../../shared/shared.module';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';
import { Ng2Rut } from 'ng2-rut';


import { navLocalesComponent } from './detalleCliente/navBarDetallesCliente/navLocales/navLocales.component';
import { BuscaClienteComponent } from './buscaCliente/buscaCliente.component';
import { DetalleClienteComponent } from './detalleCliente/detalleCliente.component';
import { NavBarDetallesClienteComponent } from './detalleCliente/navBarDetallesCliente/navBarDetallesCliente.component';
import { ListadoClienteComponent } from './listadoCliente/listadoCliente.component';
import { NavDireccionesComponent } from './detalleCliente/navBarDetallesCliente/navDirecciones/navDirecciones.component';
import { NavRelacionesComercialesComponent } from './detalleCliente/navBarDetallesCliente/navRelacionesComerciales/navRelacionesComerciales.component';
import { NavCuentasBancariasComponent } from './detalleCliente/navBarDetallesCliente/navCuentasBancarias/navCuentasBancarias.component';
import { TipoClientePopUpComponent } from '../commonPopUp/tipoClientePopUp/tipoClientePopUp.component';
import { RutAsociadoPopUpComponent } from '../commonPopUp/rutAsociadoPopUp/rutAsociadoPopUp.component';
import { GirosPopUpComponent } from '../commonPopUp/girosPopUp/girosPopUp.component';
import { LocalesPopUpComponent } from '../commonPopUp/localesPopUp/localesPopUp.component';
import { DireccionesPopUpComponent } from '../commonPopUp/direccionesPopUp/direccionesPopUp.component';
import { BancoPopUpComponent } from '../commonPopUp/bancoPopUp/bancoPopUp.component';
import { UtilGeoPopUpComponent } from '../commonPopUp/utilGeoPopUp/utilGeoPopUp.component';
import { BuscaBancoPopUpComponent } from '../commonPopUp/buscaBancoPopUp/buscaBancoPopUp.component';
import { RelacionesPopUpComponent } from '../commonPopUp/relacionesPopUp/relacionesPopUp.component';
import { RutasPopUpComponent } from '../commonPopUp/rutasPopUp/rutasPopUp.component';
import { ZonasTodasPopUpComponent } from '../commonPopUp/zonasTodasPopUp/zonasTodasPopUp.component';


const routes: Routes = [
    { path: 'buscaCliente', component: BuscaClienteComponent },
    { path: 'detalleCliente', component: DetalleClienteComponent},
    { path: 'listadoCliente', component: ListadoClienteComponent }
];

@NgModule({
    imports: [
        SharedModule,
        RouterModule.forChild(routes),
        Ng2TableModule,
        NgxDatatableModule,
        NgxPaginationModule,
        CommonPopUpModule,
        Ng2Rut
    ],
    declarations: [
        BuscaClienteComponent,
        DetalleClienteComponent,
        NavBarDetallesClienteComponent,
        ListadoClienteComponent,
        navLocalesComponent,
        NavDireccionesComponent,
        NavRelacionesComercialesComponent,
        NavCuentasBancariasComponent
    ],
    entryComponents: [ TipoClientePopUpComponent,
        RutAsociadoPopUpComponent,
        GirosPopUpComponent,
        LocalesPopUpComponent,
        DireccionesPopUpComponent,
        BancoPopUpComponent,
        UtilGeoPopUpComponent,
        BuscaBancoPopUpComponent,
        RelacionesPopUpComponent,
        RutasPopUpComponent,
        ZonasTodasPopUpComponent
        ],
    exports: [
        RouterModule
    ]
})
export class ClienteModule {
 }