import { Component, OnInit, OnDestroy, AfterViewChecked } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as _ from 'lodash';

import { NgxSpinnerService } from 'ngx-spinner';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ConsultasService } from '../../../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { BancoPopUpComponent } from '../../../../commonPopUp/bancoPopUp/bancoPopUp.component';

declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navCuentasBancarias',
  templateUrl: './navCuentasBancarias.component.html',
  styleUrls: ['./navCuentasBancarias.component.scss']
})
export class NavCuentasBancariasComponent implements OnInit, OnDestroy {
    ctaBanco: any = {};
    bancos: Array <any> = [];
    rutcliente = this.consultaBancos.selectedCliente.cli_nrutcliente;
    rutoperador = this.consultaBancos.selectedCliente.cli_nrutoperador;
    origen: string;
    bancoDos: any = {};
    detalleBancos: any = {};
    bancosModal: BsModalRef;
    reloadTabla: any;
    pActualBanco = 1;

  constructor(public http: HttpClient, private consultaBancos: ClientesService,
    private modalService: BsModalService, private setearDatos: ConsultasService,
    public cargador: NgxSpinnerService) {
  }
  public ngOnInit(): void {
    this.reloadTabla = this.setearDatos.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.listadoBancos();
  }
  ngOnDestroy() {
    if (this.reloadTabla !== undefined) {
      this.reloadTabla.unsubscribe();
    }
  }

  listadoBancos() {
    this.ctaBanco.rutCliente = this.rutcliente;
    this.ctaBanco.rutOperador = this.rutoperador;
    this.consultaBancos.getBancos(this.ctaBanco)
        .subscribe((data: any) => {
            (this.bancos = data.cuentaBanco);
    });
  }
  editarBancos(banco) {
    this.origen = 'bancos';
    const initialState = {
      titulo: 'Bancos',
      banco: banco
    };
    this.cargador.show();
      this.bancosModal = this.modalService.show(BancoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
      this.cargador.hide();
  }
  openModalBancos () {
    this.origen = 'bancos';
    const initialState = {
      titulo: 'Bancos',
      origen: 'bancos'
    };
    this.bancosModal = this.modalService.show(BancoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'bancos': {
          if (resData = 'succesBanco'){
            this.listadoBancos();
          }
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
}