import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as _ from 'lodash';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import {ConsultasService} from '../../../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { LocalesPopUpComponent } from '../../../../commonPopUp/localesPopUp/localesPopUp.component';

declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navLocales',
  templateUrl: './navLocales.component.html',
  styleUrls: ['./navLocales.component.scss']
})

export class navLocalesComponent implements OnInit, OnDestroy {
    localesClient: any = {};
    locales: Array <any> = [];
    rutcliente = this.consultaLocales.selectedCliente.cli_nrutcliente;
    rutoperador = this.consultaLocales.selectedCliente.cli_nrutoperador;
    localesModal: BsModalRef;
    origen: string;
    localDos: any = {};
    detalleLocales: any = {};
    numLocal: any;
    numNuevo: any;
    reloadTabla: any;
    pActualLocales = 1;

  constructor(public http: HttpClient, private consultaLocales: ClientesService,
    private modalService: BsModalService, private setearDatos: ConsultasService,
    public cargador: NgxSpinnerService) {
  }
  public ngOnInit(): void {
    this.reloadTabla = this.setearDatos.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.listadoLocales();
  }

  ngOnDestroy() {
    if (this.reloadTabla !== undefined) {
      this.reloadTabla.unsubscribe();
    }
  }
  listadoLocales() {
    this.localesClient.rutCliente = this.rutcliente;
    this.localesClient.rutOperador = this.rutoperador;
    this.consultaLocales.getLocales(this.localesClient)
        .subscribe((data: any) => {
            (this.locales = data.locales);
        });
  }

  editarLocales(local) {
    this.origen = 'locales';
    const initialState = {
      titulo: 'Detalle Local Número ',
      local: local
    };
    this.cargador.show();
      this.localesModal = this.modalService.show(LocalesPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
      this.cargador.hide();
  }
  openModalLocales () {
    this.origen = 'locales';
    const initialState = {
      titulo: 'Locales',
      origen: 'locales',
    };
    this.localesModal = this.modalService.show(LocalesPopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'locales': {
          if (resData = 'succesLocales') {
            this.listadoLocales();
          }
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
}