import { Component, OnInit, AfterViewChecked, OnDestroy } from '@angular/core'
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import * as _ from 'lodash';

import { NgxSpinnerService } from 'ngx-spinner';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { UtilService } from '../../../../../services/util.service';
import { ConsultasService } from '../../../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { AuthService } from '../../../../../core/consultaService/auth.service';
import { RelacionesPopUpComponent } from '../../../../commonPopUp/relacionesPopUp/relacionesPopUp.component';
import { RutasPopUpComponent } from '../../../../commonPopUp/rutasPopUp/rutasPopUp.component';
import { TipoClientePopUpComponent } from '../../../../commonPopUp/tipoClientePopUp/tipoClientePopUp.component';
import { UtilGeoPopUpComponent } from '../../../../commonPopUp/utilGeoPopUp/utilGeoPopUp.component';
import { ZonasTodasPopUpComponent } from '../../../../commonPopUp/zonasTodasPopUp/zonasTodasPopUp.component';

declare var jQuery: any;
declare var $: any;
const swal = require('sweetalert');

@Component({
  selector: 'app-navRelacionesComerciales',
  templateUrl: './navRelacionesComerciales.component.html',
  styleUrls: ['./navRelacionesComerciales.component.scss']
})
export class NavRelacionesComercialesComponent implements OnInit, OnDestroy {
  relacComer: any = {};
  relaciones: Array <any> = [];
  user: any = {};
  relacionesEdit: any = {};
  codRelacionsave: string;
  rutcliente = this.consultaRelaciones.selectedCliente.cli_nrutcliente;
  rutoperador = this.consultaRelaciones.selectedCliente.cli_nrutoperador;
  reloadTabla: any;
  relacionesComercialesModal: BsModalRef;
  rutasRelac:  BsModalRef;
  cuposModal:  BsModalRef;
  territoriosTodosModal: BsModalRef;
  origen: string;
  descripcionRela: string;
  forma: FormGroup;
  pActualRela = 1;
  mostrar: number;
  editarRela: any = {};
  checkedLunes = false;
  checkedMartes = false;
  checkedMiercoles = false;
  checkedJueves = false;
  checkedViernes = false;
  checkedSabado = false;
  frecuenciaVisita: any = {};
  tipoCobro: any = {};
  tipoPago: any = {};
  canalVenta: any = {};
  subData: any;
  subData2: any;
  prueba: Date = new Date();
  fechaString: string;
  utilGeoModal: BsModalRef;
  saveRelacion: any = {};
  requestFrecuencia: any = {
    "parnumparametro": 18
  };
  requestCobro: any = {
    "parnumparametro": 29
  };
  requestPago: any = {
  };
  estado: Array<any> = [
    {
      rco_scodestado: 'VIGEN',
      descEstado: 'Activo'
    },
    {
      rco_scodestado: 'NOVIG',
      descEstado: 'Inactivo'
    }
  ];

  constructor(public http: HttpClient, fb: FormBuilder, private utilService: UtilService,
    private consultaRelaciones: ClientesService, private modalService: BsModalService,
    private setearDatos: ConsultasService, public loader: NgxSpinnerService, private authService: AuthService ) {
      this.forma = new FormGroup ({
        codTerritorio: new FormControl({value: '', disabled: true }, Validators.required),
        idTerritorio: new FormControl('', Validators.required),
        territorio: new FormControl({value: '', disabled: true }),
        codCupo: new FormControl({value: 0, disabled: true }),
        cupo: new FormControl({value: 0, disabled: true }),
        frecVisita: new FormControl(''),
        dias: new FormControl(0),
        codRuta1: new FormControl (''),
        ruta1Id: new FormControl({value: '', disabled: true }),
        ruta1: new FormControl({value: '', disabled: true }),
        codRuta2: new FormControl (''),
        ruta2Id: new FormControl({value: '', disabled: true }),
        ruta2: new FormControl({value: '', disabled: true }),
        codRuta3: new FormControl (''),
        ruta3Id: new FormControl({value: '', disabled: true }),
        ruta3: new FormControl({value: '', disabled: true }),
        codRuta4: new FormControl (''),
        ruta4Id: new FormControl({value: '', disabled: true }),
        ruta4: new FormControl({value: '', disabled: true }),
        codRuta5: new FormControl (''),
        ruta5Id: new FormControl({value: '', disabled: true }),
        ruta5: new FormControl({value: '', disabled: true }),
        codRuta6: new FormControl (''),
        ruta6Id: new FormControl({value: '', disabled: true }),
        ruta6: new FormControl({value: '', disabled: true }),
        codDirVisita: new FormControl(''),
        dirVisita: new FormControl({value: '', disabled: true }),
        fechaCrea: new FormControl({value: '', disabled: true }),
        codDirEntrega: new FormControl(''),
        dirEntrega: new FormControl({value: '', disabled: true }),
        canalVenta: new FormControl(),
        codDirPago: new FormControl(''),
        dirPago: new FormControl({value: '', disabled: true }),
        tipoCobro: new FormControl(''),
        estado: new FormControl(''),
        tipoPago: new FormControl(''),
        numLocal: new FormControl(''),
        observaciones: new FormControl('')
    });
    this.mostrar = 1;
     }

  ngOnInit() {
    this.subData = this.setearDatos.datosGeo$.subscribe(resData =>{
      this.setData(resData);
    });
    this.subData2 = this.setearDatos.datos$.subscribe(resData =>{
      this.setData(resData);
    });
    this.user = this.authService.getDatosUser();
    this.listadoRelaciones();
    this.frecuenciaVisitas();
    this.tiposCobros();
    this.tiposPagos();
    this.canalesVentas();
  }
  ngOnDestroy() {
    if (this.reloadTabla !== undefined) {
      this.reloadTabla.unsubscribe();
    }
    if (this.subData2 !== undefined) {
      this.subData.unsubscribe();
    }
  }
  listadoRelaciones() {
    this.relacComer.rutCliente = this.rutcliente;
    this.relacComer.rutOperador = this.rutoperador;
    this.consultaRelaciones.getRelaciones(this.relacComer)
        .subscribe((data: any) => {
            (this.relaciones = data.localesRelaciones);
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  frecuenciaVisitas() {
    /* this.setearDatos.getTipoCliente(this.requestFrecuencia)
        .subscribe((data: any) => {
            (this.frecuenciaVisita = data.general);
        },
      error => {
        this.loader.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      }); */
    this.setearDatos.getTipoPagoCliente(this.requestPago)
      .subscribe((data: any) => {
          //(this.tipoPago = data.general);
          this.frecuenciaVisita = data.general.filter(function (tipoPagoItem) {
            return tipoPagoItem.par_numparametro === 18;
          });
      },
      error => {
        this.loader.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      });
  }
  tiposCobros() {
    /* this.setearDatos.getTipoCliente(this.requestCobro)
        .subscribe((data: any) => {
            (this.tipoCobro = data.general);
        },
      error => {
        this.loader.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      }); */
      this.setearDatos.getTipoPagoCliente(this.requestPago)
        .subscribe((data: any) => {
            //(this.tipoPago = data.general);
            this.tipoCobro = data.general.filter(function (tipoPagoItem) {
              return tipoPagoItem.par_numparametro === 29;
            });
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  tiposPagos() {
    this.setearDatos.getTipoPagoCliente(this.requestPago)
        .subscribe((data: any) => {
            //(this.tipoPago = data.general);
            this.tipoPago = data.general.filter(function (tipoPagoItem) {
              return tipoPagoItem.par_numparametro === 7;
            });
      
            
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  canalesVentas() {
    this.setearDatos.getCanalesVenta()
        .subscribe((data: any) => {
            (this.canalVenta = data.canalesVenta);
        },
        error => {
          this.loader.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        });
  }
  openModalRelaciones () {
    this.origen = 'relaciones';
    const initialState = {
      titulo: 'Relaciones Comerciales',
      origen: 'relaciones',
    };
    this.relacionesComercialesModal = this.modalService.show(
      RelacionesPopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  openModalTerritoriosTodos() {
    this.origen = 'territorios';
    const initialState = {
      parametro: this.rutoperador,
      origen: 'territorios'
    };
    this.territoriosTodosModal = this.modalService.show(ZonasTodasPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  openModalCupo () {
    this.origen = 'cupo';
    const initialState = {
      filtro: this.codRelacionsave,
      origen: 'cupo',
    };
    this.cuposModal = this.modalService.show(TipoClientePopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  openModalDireccionRela () {
    this.origen = 'relaciones';
    const initialState = {
      titulo: 'Relaciones Comerciales',
      origen: 'relaciones',
    };
    this.relacionesComercialesModal = this.modalService.show(
      RelacionesPopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  openModalRutas (data: string) {
    this.origen = data;
    const initialState = {
      titulo: 'Rutas',
      origen: data,
    };
    this.rutasRelac = this.modalService.show(RutasPopUpComponent, {initialState, backdrop: 'static', keyboard: false });
  }
  openModalDireBanco(data: string) {
    this.origen = data;
    const initialState = {
      titulo: 'Dirección',
      origen: data
    };
    this.utilGeoModal = this.modalService.show(UtilGeoPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  validaMartes() {
    this.forma.controls.codRuta2.setValue('');
    this.forma.controls.ruta2.setValue('');
    this.checkedMartes = false;
    this.forma.controls.ruta2Id.disable();
  }
  validaLunes() {
    this.forma.controls.codRuta1.setValue('');
    this.forma.controls.ruta1.setValue('');
    this.checkedLunes = false;
    this.forma.controls.ruta1Id.disable();
  }
  validaMiercoles() {
    this.forma.controls.codRuta3.setValue('');
    this.forma.controls.ruta3.setValue('');
    this.checkedMiercoles = false;
    this.forma.controls.ruta3Id.disable();
  }
  validaJueves() {
    this.forma.controls.codRuta4.setValue('');
    this.forma.controls.ruta4.setValue('');
    this.checkedJueves = false;
    this.forma.controls.ruta4Id.disable();
  }
  validaViernes() {
    this.forma.controls.codRuta5.setValue('');
    this.forma.controls.ruta5.setValue('');
    this.checkedViernes = false;
    this.forma.controls.ruta5Id.disable();
  }
  validaSabado() {
    this.forma.controls.codRuta6.setValue('');
    this.forma.controls.ruta6.setValue('');
    this.checkedSabado = false;
    this.forma.controls.ruta6Id.disable();
  }
  setData(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'lunes': {
          this.forma.controls.ruta1.setValue(resData.rut_sdesruta);
          this.forma.controls.codRuta1.setValue(resData.rut_scodruta);
          this.forma.controls.ruta1Id.enable();
          this.checkedLunes = true;
          break;
        }
        case 'martes': {
          this.forma.controls.ruta2.setValue(resData.rut_sdesruta);
          this.forma.controls.codRuta2.setValue(resData.rut_scodruta);
          this.forma.controls.ruta2Id.enable();
          this.checkedMartes = true;
          break;
        }
        case 'miercoles': {
          this.forma.controls.ruta3.setValue(resData.rut_sdesruta);
          this.forma.controls.codRuta3.setValue(resData.rut_scodruta);
          this.forma.controls.ruta3Id.enable();
          this.checkedMiercoles = true;
          break;
        }
        case 'jueves': {
          this.forma.controls.ruta4.setValue(resData.rut_sdesruta);
          this.forma.controls.codRuta4.setValue(resData.rut_scodruta);
          this.forma.controls.ruta4Id.enable();
          this.checkedJueves = true;
          break;
        }
        case 'viernes': {
          this.forma.controls.ruta5.setValue(resData.rut_sdesruta);
          this.forma.controls.codRuta5.setValue(resData.rut_scodruta);
          this.forma.controls.ruta5Id.enable();
          this.checkedViernes = true;
          break;
        }
        case 'sabado': {
          this.forma.controls.ruta6.setValue(resData.rut_sdesruta);
          this.forma.controls.codRuta6.setValue(resData.rut_scodruta);
          this.forma.controls.ruta6Id.enable();
          this.checkedSabado = true;
          break;
        }
        case 'cupo': {
          this.forma.controls.cupo.setValue(resData.par_deslargo01);
          this.forma.controls.codCupo.setValue(resData.par_codparametro02);
          break;
        }
        case 'direVisitas': {
          this.forma.controls.dirVisita.setValue(resData.dic_sdireccion);
          this.forma.controls.codDirVisita.setValue(resData.dic_niddireccion);
          break;
        }
        case 'direEntregas': {
          this.forma.controls.dirEntrega.setValue(resData.dic_sdireccion);
          this.forma.controls.codDirEntrega.setValue(resData.dic_niddireccion);
          break;
        }
        case 'direPagos': {
          this.forma.controls.dirPago.setValue(resData.dic_sdireccion);
          this.forma.controls.codDirPago.setValue(resData.dic_niddireccion);
          break;
        }
        case 'territorios': {
          this.forma.controls.territorio.setValue(resData.ter_sdesterritorio);
          this.forma.controls.codTerritorio.setValue(resData.ter_scodterritorio);
          this.forma.controls.idTerritorio.setValue(resData.ter_nidterritorio);
          break;
        }
        case 'relaciones': {
          if (resData = 'succesRela') {
            this.listadoRelaciones();
          }
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
  editarRelacion (relacion) {
    this.descripcionRela = relacion.rco_nnumlocal + '-' + relacion.rco_scodrelacioncomercial;
    this.codRelacionsave = relacion.rco_scodrelacioncomercial;
    this.editarRela.codRelacion = relacion.rco_scodrelacioncomercial;
    this.editarRela.numLocal = relacion.rco_nnumlocal;
    this.editarRela.rutOperador = relacion.rco_nrutoperador;
    this.editarRela.rutcliente = relacion.rco_nrutcliente;
    this.consultaRelaciones.getRelacionesCarga(this.editarRela)
        .subscribe((data: any) => {
          if (data.relacionComercial.length === 0 && data.error.codError === 0 ){
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } else {
              (this.relacionesEdit = data.relacionComercial);
              this.forma.controls.territorio.setValue(this.relacionesEdit[0].ter_sdesterritorio);
              if (this.relacionesEdit[0].ter_scodterritorio !== 0) {
                this.forma.controls.codTerritorio.setValue(this.relacionesEdit[0].ter_scodterritorio);
              } else {
                this.forma.controls.codTerritorio.setValue('');
              }
              if (this.relacionesEdit[0].rco_nidterritorio !== 0) {
                this.forma.controls.idTerritorio.setValue(this.relacionesEdit[0].rco_nidterritorio);
              } else {
                this.forma.controls.idTerritorio.setValue('');
              }
              this.forma.controls.codCupo.setValue(this.relacionesEdit[0].rco_scodcupocredito);
              this.forma.controls.cupo.setValue(this.relacionesEdit[0].rco_ncupocredito);
              this.forma.controls.frecVisita.setValue(this.relacionesEdit[0].rco_scodfrecuenciavisita);
              this.forma.controls.dias.setValue(this.relacionesEdit[0].rco_ndiascredito);

              if (this.relacionesEdit[0].rco_sindlunes === 'S') {
                this.forma.controls.codRuta1.setValue(this.relacionesEdit[0].rco_scodrutalunes);
                this.checkedLunes = true;
                this.forma.controls.ruta1Id.enable();
              } else {
                this.checkedLunes = false;
                this.forma.controls.ruta1Id.disable();
              }
              this.forma.controls.ruta1.setValue(this.relacionesEdit[0].sdesrutalunes);

              if (this.relacionesEdit[0].rco_sindmartes === 'S') {
                this.forma.controls.codRuta2.setValue(this.relacionesEdit[0].rco_scodrutamartes);
                this.checkedMartes = true;
                this.forma.controls.ruta2Id.enable();
              } else {
                this.checkedMartes = false;
                this.forma.controls.ruta2Id.disable();
              }
              this.forma.controls.ruta2.setValue(this.relacionesEdit[0].sdesrutamartes);

              if (this.relacionesEdit[0].rco_sindmiercoles === 'S') {
                this.forma.controls.codRuta3.setValue(this.relacionesEdit[0].rco_scodrutamiercoles);
                this.checkedMiercoles = true;
                this.forma.controls.ruta3Id.enable();
              } else {
                this.checkedMiercoles = false;
                this.forma.controls.ruta3Id.disable();
              }
              this.forma.controls.ruta3.setValue(this.relacionesEdit[0].sdesrutamiercoles);

                if (this.relacionesEdit[0].rco_sindjueves === 'S') {
                  this.forma.controls.codRuta4.setValue(this.relacionesEdit[0].rco_scodrutajueves);
                  this.checkedJueves = true;
                  this.forma.controls.ruta4Id.enable();
                } else {
                  this.checkedJueves = false;
                  this.forma.controls.ruta4Id.disable();
                }
              this.forma.controls.ruta4.setValue(this.relacionesEdit[0].sdesrutajueves);

                if (this.relacionesEdit[0].rco_sindviernes === 'S') {
                  this.forma.controls.codRuta5.setValue(this.relacionesEdit[0].rco_scodrutaviernes);
                  this.checkedViernes = true;
                  this.forma.controls.ruta5Id.enable();
                }  else {
                  this.checkedViernes = false;
                  this.forma.controls.ruta5Id.disable();
                }
              this.forma.controls.ruta5.setValue(this.relacionesEdit[0].sdesrutaviernes);

                if (this.relacionesEdit[0].rco_sindsabado === 'S') {
                  this.forma.controls.codRuta6.setValue(this.relacionesEdit[0].rco_scodrutasabado);
                  this.checkedSabado = true;
                  this.forma.controls.ruta6Id.enable();
                }  else {
                  this.checkedSabado = false;
                  this.forma.controls.ruta6Id.disable();
                }
              this.forma.controls.ruta6.setValue(this.relacionesEdit[0].sdesrutasabado);
              this.forma.controls.codDirVisita.setValue(this.relacionesEdit[0].rco_niddireccionvisita);
              this.forma.controls.dirVisita.setValue(this.relacionesEdit[0].direccionvisita);
              this.forma.controls.fechaCrea.setValue(
                this.utilService.dateStringServiceToStringFormat(this.relacionesEdit[0].rco_dfhocreacion));
              this.forma.controls.codDirEntrega.setValue(this.relacionesEdit[0].rco_niddireccionentrega);
              this.forma.controls.dirEntrega.setValue(this.relacionesEdit[0].direccionentrega);
              this.forma.controls.canalVenta.setValue(this.relacionesEdit[0].rco_scodcanalventa);
              this.forma.controls.codDirPago.setValue(this.relacionesEdit[0].rco_niddireccionpago);
              this.forma.controls.dirPago.setValue(this.relacionesEdit[0].direccionpago);
              this.forma.controls.tipoCobro.setValue(this.relacionesEdit[0].rco_scodtipocobro);
              this.forma.controls.estado.setValue(this.relacionesEdit[0].rco_scodestado);
              this.forma.controls.tipoPago.setValue(this.relacionesEdit[0].rco_scodtipopago);
              this.forma.controls.numLocal.setValue(this.relacionesEdit[0].rco_nnumlocal);
              this.forma.controls.observaciones.setValue(this.relacionesEdit[0].rco_scomentario);

          }
        });

    this.mostrar = 2;


  }
  actualizaRelacion() {
    console.log(this.forma.controls.codTerritorio.value);
    if  (this.forma.valid) {
      $('*').removeClass('error-form');
      this.loader.show();

      this.saveRelacion.nCupoCredito = this.forma.controls.cupo.value;
      this.saveRelacion.nDiasCredito = this.forma.controls.dias.value;
      this.saveRelacion.nIdDireccionPago = this.forma.controls.codDirPago.value;
      this.saveRelacion.nIdTerritorio = this.forma.controls.idTerritorio.value;
      this.saveRelacion.nIddDireccionEntrega = this.forma.controls.codDirEntrega.value;
      this.saveRelacion.nIddireccionVisita = this.forma.controls.codDirVisita.value;
      this.saveRelacion.nNumLocal = this.forma.controls.numLocal.value;
      this.saveRelacion.nRunUsuarioActualiza = this.user.nrunusuario;
      this.saveRelacion.nRutCliente = this.rutcliente;
      this.saveRelacion.nRutOperador = this.rutoperador;
      this.saveRelacion.sCodCanalVenta = this.forma.controls.canalVenta.value;
      this.saveRelacion.sCodCupoCredito = this.forma.controls.codCupo.value;
      this.saveRelacion.sCodEstado = this.forma.controls.estado.value;
      this.saveRelacion.sCodFrecuenciaVisita = this.forma.controls.frecVisita.value;
      this.saveRelacion.sCodRelacionComercial = this.codRelacionsave;
      this.saveRelacion.sCodRutaDomingo = null;
      this.saveRelacion.sCodRutaJueves = this.forma.controls.codRuta4.value;
      this.saveRelacion.sCodRutaLunes = this.forma.controls.codRuta1.value;
      this.saveRelacion.sCodRutaMartes = this.forma.controls.codRuta2.value;
      this.saveRelacion.sCodRutaMiercoles = this.forma.controls.codRuta3.value;
      this.saveRelacion.sCodRutaSabado = this.forma.controls.codRuta6.value;
      this.saveRelacion.sCodRutaViernes = this.forma.controls.codRuta5.value;
      this.saveRelacion.sCodTipoCobro = this.forma.controls.tipoCobro.value;
      this.saveRelacion.sCodTipoPago = this.forma.controls.tipoPago.value;
      this.saveRelacion.sComentario = this.forma.controls.observaciones.value;
      this.saveRelacion.sIndDomingo = 'N';

      if (this.checkedJueves) {
        this.saveRelacion.sIndJueves = 'S';
      } else {
        this.saveRelacion.sIndJueves = 'N';
      }

      if (this.checkedLunes) {
        this.saveRelacion.sIndLunes = 'S';
      } else {
        this.saveRelacion.sIndLunes = 'N';
      }
      if (this.checkedMartes === true) {
        this.saveRelacion.sIndMartes = 'S';
      } else {
        this.saveRelacion.sIndMartes = 'N';
      }
      if (this.checkedMiercoles === true) {
        this.saveRelacion.sIndMiercoles = 'S';
      } else {
        this.saveRelacion.sIndMiercoles = 'N';
      }
      if (this.checkedSabado === true) {
        this.saveRelacion.sIndSabados = 'S';
      } else {
        this.saveRelacion.sIndSabados = 'N';
      }
      if (this.checkedViernes === true) {
        this.saveRelacion.sIndViernes = 'S';
      } else {
        this.saveRelacion.sIndViernes = 'N';
      }
      this.consultaRelaciones.saveRelacionesActualiza(this.saveRelacion)
          .subscribe((data: any) => {
            if (data.error.codError === 0) {
              swal('Bien', 'Los datos han sido actualizados satisfactoriamente.', 'success' );
              this.listadoRelaciones();
              this.mostrar = 1;
              this.loader.hide();
              } else {
                swal('Atención', data.error.msjError, 'error');
                this.loader.hide();
              }
      },
      error => {
        this.loader.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      });
    } else {
      swal('Atención', 'Existen campos requeridos no ingresados', 'error' );
      if (this.forma.controls.codTerritorio.value === 0 || this.forma.controls.codTerritorio.value === '') {
        $('[name=codTerritorio]').addClass('error-form');
      } else {
        $('[name=codTerritorio]').removeClass('error-form');
      }
    }

  }
}
