import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as _ from 'lodash';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { NgxSpinnerService } from 'ngx-spinner';
import {ConsultasService} from '../../../../../core/consultaService/consultas.service';
import { ClientesService } from '../../../../../core/consultaService/clientes.service';
import { DireccionesPopUpComponent } from '../../../../commonPopUp/direccionesPopUp/direccionesPopUp.component';
declare var jQuery: any;
declare var $: any;

@Component({
  selector: 'app-navDirecciones',
  templateUrl: './navDirecciones.component.html',
  styleUrls: ['./navDirecciones.component.scss']
})
export class NavDireccionesComponent implements OnInit, OnDestroy {
    direccionesClient: any = {};
    direcciones: Array <any> = [];
    rutcliente = this.consultaDirecciones.selectedCliente.cli_nrutcliente;
    rutoperador = this.consultaDirecciones.selectedCliente.cli_nrutoperador;
    origen: string;
    direccionDos: any = {};
    detalleDirecciones: any = {};
    direccionesModal: BsModalRef;
    reloadTabla: any;
    pActualDirecciones = 1;

  constructor(private consultaDirecciones: ClientesService, public http: HttpClient,
    private modalService: BsModalService, private setearDatos: ConsultasService,
    public cargador: NgxSpinnerService) {
  }
  public ngOnInit(): void {
    this.reloadTabla = this.setearDatos.datos$.subscribe(resData => {
      this.setTipo(resData);
    });
    this.listadoDirecciones();
  }
  ngOnDestroy() {
    if (this.reloadTabla !== undefined) {
      this.reloadTabla.unsubscribe();
    }
  }
  listadoDirecciones() {
    this.direccionesClient.rutCliente = this.rutcliente;
    this.direccionesClient.rutOperador = this.rutoperador;
    this.consultaDirecciones.getDirecciones(this.direccionesClient)
        .subscribe((data: any) => {
          (this.direcciones = data.direcciones);
    },
    error => {
    });
  }
  editarDirecciones(direccion) {
    this.origen = 'direcciones';
    const initialState = {
      titulo: 'Direcciones',
      direccion: direccion
    };
    this.cargador.show();
      this.direccionesModal = this.modalService.show(DireccionesPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
      this.cargador.hide();
  }
  openModalDirecciones () {
    this.origen = 'direcciones';
    const initialState = {
      titulo: 'Direcciones',
      origen: 'direcciones'
    };
    this.direccionesModal = this.modalService.show(DireccionesPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
  setTipo(resData) {
    if (resData !== 0) {
      switch (this.origen) {
        case 'direcciones': {
          if (resData === 'succesDir') {
            this.listadoDirecciones();
          }
          break;
        }
      }
    }
    $('body').removeClass('modal-open');
  }
}